import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { UserManagementService } from 'src/app/Shared/Services/user-management.service';

@Component({
  selector: 'app-user-login',
  templateUrl: './user-login.component.html',
  styleUrls: ['./user-login.component.css']
})
export class UserLoginComponent implements OnInit {
  MyForm: FormGroup;
  USMR:FormGroup;
  SuspendedUserData: any;
  UnSuspensionMessage: any;
  SuspendedUserName:any;
  Message:any;
  constructor(
    private _FormBuilder: FormBuilder,
    private _Router: Router,
    private _UserManagementService: UserManagementService,
    private _FlashMessages: FlashMessagesService) {
    this.MyFormModel();
    this.USMRModel();
  }

  ngOnInit(): void {
  }

  // Starting Form and From Submission COde
  MyFormModel() {
    this.MyForm = this._FormBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(15)]],
    });
  }

  UserLogin() {
    var LoginFormValues = this.MyForm.value;
    this._UserManagementService.UserLogin(LoginFormValues).subscribe((DataComingFromBackEnd: any) => {
      if (DataComingFromBackEnd.Data != null) {
        this._UserManagementService.LogIn(DataComingFromBackEnd);
      }
      else {
        this._FlashMessages.show(DataComingFromBackEnd.Message, { cssClass: 'alert-danger', timeout: 7000 });
        this.SuspendedUserData = DataComingFromBackEnd.Result;
        this.SuspendedUserName = this.SuspendedUserData.Name;
      }
    })
  }

 USMRModel(){
    this.USMR=this._FormBuilder.group({
      Id:'',
      USM:'',
   })
 }

  UnSuspendUserApplication() {
    this.USMR.get('Id').setValue(this.SuspendedUserData._id);
    const _SuspendedUserRequestBody = this.USMR.value;
    this._UserManagementService.SendUnSuspensionRequest(_SuspendedUserRequestBody).subscribe((DataComingFromBackEnd:any)=>{
      this.Message=DataComingFromBackEnd.Message;
    })

    // this._UserManagementService.SendUnSuspensionRequest(_SuspendedUserId, _Message).subscribe(DataComingFromBackEnd => {
    //   console.log(DataComingFromBackEnd);
    // })
  }
  // Ending Form and From Submission COde
}
