import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {UserRegisterationUrl} from '../../../configuration/GlobalConstants';
import {UserLoginUrl} from '../../../configuration/GlobalConstants';
import {UpdateWebsiteStatsActiveUsersUrl} from '../../../configuration/GlobalConstants';
import {DeleteUserUrl} from '../../../configuration/GlobalConstants';
import {DecrementWebsiteActiveUsersUrl} from '../../../configuration/GlobalConstants';
import  {SuspendUserUrl} from '../../../configuration/GlobalConstants';
import {UnSuspendUserUrl} from '../../../configuration/GlobalConstants';
import {PostUnSuspensionMessageRequestUrl} from '../../../configuration/GlobalConstants';
import {GetUnSuspensionBodyRequestUrl} from '../../../configuration/GlobalConstants';
import {DeleteUnSuspensionRequestUrl} from '../../../configuration/GlobalConstants';
import {GetAllUsersUrl} from '../../../configuration/GlobalConstants';
import {GetSuspendedUsersUrl} from '../../../configuration/GlobalConstants';



@Injectable({
  providedIn: 'root'
})
export class UserManagementService {

  constructor(private _HttpClient:HttpClient,private _Router:Router) { }

  RegisterNewUser(UserData){
    return this._HttpClient.post(UserRegisterationUrl,UserData);
  }

  UserLogin(UserLoginData){ return this._HttpClient.post(UserLoginUrl,UserLoginData)};

  SuspendUser(UserId){
    return this ._HttpClient.put(SuspendUserUrl+`${UserId}`,null);
  }

  DeleteUser(ImageUrl){
    return this._HttpClient.delete(DeleteUserUrl+`${ImageUrl}`);
  }

  UnSuspendUser(UserId){
    return this._HttpClient.put(UnSuspendUserUrl+`${UserId}`,null);
  }
  GetAllUsers(){
    return this._HttpClient.get(GetAllUsersUrl);
  }

  CheckIfUserisLoggedIn(){
    return localStorage.getItem('access_user_token') != null;
  }
  
  LogIn(DataComingFromBackEnd){
    localStorage.setItem('access_user_token',DataComingFromBackEnd.Token);
        this._Router.navigate(['user']);
  }

  LogOut(){
    localStorage.clear();
    this._Router.navigate(['login']);
  }

DecrementActiveUsers(){
    var StatsId="5fef8d9ceaee8010b00fbcf0"
    const _Decrement='a';
    return this._HttpClient.put(DecrementWebsiteActiveUsersUrl + `${StatsId}`,_Decrement)
}

  UpdateActiveUsers(){
    var StatsId="5fef8d9ceaee8010b00fbcf0"
    const _Increment='a';
    return this._HttpClient.put(UpdateWebsiteStatsActiveUsersUrl + `${StatsId}`,_Increment)
  }

  SendUnSuspensionRequest(data){
    return this._HttpClient.post(PostUnSuspensionMessageRequestUrl,data);
  }

  GetSuspendedUsers(){
    return this._HttpClient.get(GetSuspendedUsersUrl);
  }

  GetUnSuspensionBody(ArrayofUserId){
    const requestData:any = {data:ArrayofUserId}
    return this._HttpClient.post(GetUnSuspensionBodyRequestUrl ,requestData);
  }

  DeleteUnSuepensionBody(SandBoxId){
    return this._HttpClient.delete(DeleteUnSuspensionRequestUrl+`${SandBoxId}`);
  }
}
