import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ECommerceDataServiceService } from 'src/app/Shared/Services/e-commerce-data-service.service';
import { GetFeatureCardDirectoryUrl } from 'src/configuration/GlobalConstants';

@Component({
  selector: 'app-feature-card-analytics',
  templateUrl: './feature-card-analytics.component.html',
  styleUrls: ['./feature-card-analytics.component.css']
})
export class FeatureCardAnalyticsComponent implements OnInit {
  //Class Properties
  UpdateMessage:any;
  ID:any;
  UpdateData:FormGroup;
  Quantity: any;
  getData: FormGroup;
  ProductOutOfStock: any = [];
  attachmentList: any = [];
  ImageData: any=[];
  message: string;
  Url = GetFeatureCardDirectoryUrl;
  Name: any;
  ImageDataOutOfStock: any=[];
  //Class Properties
 
  constructor(private _Router: Router, private _FormBuilder: FormBuilder, private _FlashMessages: FlashMessagesService, private _ECommerceService: ECommerceDataServiceService) {this.createFormToUpdateValue()}

  ngOnInit(): void {
    this._ECommerceService.GetFeatureCards().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].quantity === 0) {
          this.ProductOutOfStock.push(data[i]);
          this.ImageDataOutOfStock.push(data[i].imgDetails[0].imageUrl);
        }
        else {
          this.attachmentList.push(data[i]);
          this.ImageData.push(data[i].imgDetails[0].imageUrl);
        }
      }
    });
    this.attachmentList=[];
    this.ProductOutOfStock=[];
  }

  DeleteCard(ProductName) {
    this._ECommerceService.DeleteFeatureCard(ProductName).subscribe((response: any) => {
      this.message = response.message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-danger', timeout: 22000 });
      this.ngOnInit();
    })
  }

  GetDataForId(id) {
    this.ID=id;
    this._ECommerceService.GetFeatureProductsById(id).subscribe((Data: any) => {
      this.Quantity = Data.Result.quantity;
      this.Name = Data.Result.productName;
      this.UpdateData.reset();
    })
  }


  
  createFormToUpdateValue(){
    this.UpdateData=this._FormBuilder.group({
      UpdateQuantity:'',
    })
  }

  UpdateTheQuantity() { 
    const data=this.UpdateData.value;
    if (data.UpdateQuantity<=0) {
      this.UpdateMessage=`Quanitity cannot be 0 or less than 0`;
    } else {
      this._ECommerceService.UpdateFeatureCardQuantityById(this.ID,data).subscribe((DataComingFromBackEnd:any)=>{
        this.UpdateMessage=DataComingFromBackEnd.message;
        this.ngOnInit();
      })
    }
  }

}
