import { Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { CheckOutService } from 'src/app/Shared/Services/check-out.service';

@Component({
  selector: 'app-thank-you',
  templateUrl: './thank-you.component.html',
  styleUrls: ['./thank-you.component.css']
})
export class ThankYouComponent implements OnInit {

  constructor(private _CheckOutService: CheckOutService, private _Router: Router) { }
  _CartTotal: any;
  _CartTotalIncludingShipmentCharges: any;
  _OrderDetails: any;
  _SenderDetails: any;
  _TotalItems: any;
  _Check: any;
  ngOnInit(): void {
    let _Order = this._CheckOutService.GetCheckOut();
    if (_Order) {
      this._Check = true;
      this._CartTotal = _Order.CartTotal;
      this._CartTotalIncludingShipmentCharges = _Order.CartTotalIncludingShipmentCharges;
      this._OrderDetails = _Order.OrderDetails;
      this._SenderDetails = _Order.SenderDetails;
      this._TotalItems = _Order.TotalItems;
    } else {
      this._Check = false;
    }
  }

  GoHome() {
    this._CheckOutService.ClearCheckOutData();
    this._Router.navigate(['home']);
  }
}