import { AboutApplicationComponent } from './AdminPortal/adminComponents/about-application/about-application.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutComponent } from './about/about.component';
import { AdminPortalComponent } from './AdminPortal/admin-portal/admin-portal.component';
import { CardAnalyticsComponent } from './AdminPortal/adminComponents/card-analytics/card-analytics.component';
import { CreateCardComponent } from './AdminPortal/adminComponents/create-card/create-card.component';
import { CreateFeatureCardComponent } from './AdminPortal/adminComponents/create-feature-card/create-feature-card.component';
import { DashboardComponent } from './AdminPortal/adminComponents/dashboard/dashboard.component';
import { FeatureCardAnalyticsComponent } from './AdminPortal/adminComponents/feature-card-analytics/feature-card-analytics.component';
import { AboutUsComponentSettingsComponent } from './AdminPortal/adminComponents/general-settings/about-us-component-settings/about-us-component-settings.component';
import { GeneralSettingsComponent } from './AdminPortal/adminComponents/general-settings/general-settings.component';
import { SettingsComponent } from './AdminPortal/adminComponents/settings/settings.component';
import { UserManagementComponent } from './AdminPortal/adminComponents/user-management/user-management.component';
import { ContactComponent } from './contact/contact.component';
import { HomeComponent } from './home/home.component';
import { AdminLoginComponent } from './Management/AdminManagement/admin-login/admin-login.component';
import { AdminRegisterationComponent } from './Management/AdminManagement/admin-registeration/admin-registeration.component';
import { UserLoginComponent } from './Management/UserManagement/user-login/user-login.component';
import { UserRegisterationComponent } from './Management/UserManagement/user-registeration/user-registeration.component';
import { CartComponent } from './products/cart/cart.component';
import { CheckoutComponent } from './products/checkout/checkout.component';
import { ProductsComponent } from './products/products.component';
import { ViewCartComponent } from './products/view-cart/view-cart.component';
import { ViewProductComponent } from './products/view-product/view-product.component';
import { AdminGuardGuard } from './Shared/Guards/admin-guard.guard';
import { UserGuardGuard } from './Shared/Guards/user-guard.guard';
import { UserPortalComponent } from './UserPortal/user-portal/user-portal.component';
import { UserDashboradComponent } from './UserPortal/userComponents/userDashBoard/user-dashborad/user-dashborad.component';
import { ThankYouComponent } from './products/thank-you/thank-you.component';

const routes: Routes = [
  {path:'',component:HomeComponent},
  {path:'home',component:HomeComponent},
  {path:'product',component:ProductsComponent},
  {path: 'viewProduct', component: ViewProductComponent},
  {path:'CheckOut',component:CheckoutComponent},
  {path:'contact',component:ContactComponent},
  {path:'about',component:AboutComponent},
  {path:'login',component:UserLoginComponent},
  {path:'register',component:UserRegisterationComponent},
  {path:'AdminRegisteration',component:AdminRegisterationComponent},
  {path:'AdminLogin',component:AdminLoginComponent},
  {path:'Cart',component:CartComponent},
  {path:'ViewCart',component:ViewCartComponent},
  {path:'ThankYou',component:ThankYouComponent},
  {path:'admin',canActivate:[AdminGuardGuard],component:AdminPortalComponent,
    children:[
      {path:'',component:DashboardComponent},
      {path:'dashboard',component:DashboardComponent},
      {path:'CreateCard',component:CreateCardComponent},
      {path:'CardAnalytics',component:CardAnalyticsComponent},
      {path:'CreateFeatureCard',component:CreateFeatureCardComponent},
      {path:'FeatureCardAnalytics',component:FeatureCardAnalyticsComponent},
      {path:'UserManagement',component:UserManagementComponent},
      {path:'Settings',component:SettingsComponent},
      {path: 'GeneralSettings',component:GeneralSettingsComponent},
      {path:'AboutUsComponentSettings',component:AboutUsComponentSettingsComponent},
      {path:'AboutApplication',component:AboutApplicationComponent}
    ]
},
{path:'user',canActivate:[UserGuardGuard],component:UserPortalComponent,
children:[
  {path:'',component:UserDashboradComponent},
  {path:'userDashboard',component:UserDashboradComponent}
]
}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  
})
export class AppRoutingModule { }
