import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import { GeneralSettingsService } from 'src/app/Shared/Services/general-settings.service';
import { FlashMessagesService } from 'angular2-flash-messages';
import {GetProductCategoryImagesUrl} from '../../../../configuration/GlobalConstants';
import {GetFooterImageFolderUrl} from '../../../../configuration/GlobalConstants';

@Component({
  selector: 'app-general-settings',
  templateUrl: './general-settings.component.html',
  styleUrls: ['./general-settings.component.css'],
})
export class GeneralSettingsComponent implements OnInit {
  @ViewChild('FileSelect') FileSelect: ElementRef;
  @ViewChild('FooterImage') FooterImage: ElementRef;
  @ViewChild('CardOne') CardOne: ElementRef;
  @ViewChild('CardTwo') CardTwo: ElementRef;
  @ViewChild('CardThree') CardThree: ElementRef;
  @ViewChild('CardFour') CardFour: ElementRef;
  ProductUrl = GetProductCategoryImagesUrl;
  FooterImageFolderUrl =  GetFooterImageFolderUrl;
  HeadingFormData:any;
  VideoData: any;
  message: any;
  FirstImage: any;
  SecondImage: any;
  ThirdImage: any;
  FourthImage: any;
  ImageUrlOne: any;
  ImageUrlTwo:any;
  ImageUrlThree:any;
  ImageUrlFour:any;
  FooterImageData:any;
  FooterImageUrl:any;
  CardOneStyle={
    'background-image':'',
    'background-repeat':'no-repeat'
  };

  CardTwoStyle={
    'background-image':'',
    'background-repeat':'no-repeat'
  };

  CardThreeStyle={
    'background-image':'',
    'background-repeat':'no-repeat'
  };

  CardFourStyle={
    'background-image':'',
    'background-repeat':'no-repeat'
  };

  FooterStyle={
    'background-image':'',
    'background-repeat':'no-repeat',
    'background-size': 'cover',
    'background-attachment': 'fixed',
    'background-position': 'center',
  }
  // [ngStyle]="{'background-image':'url('+color+')'}"

  constructor(private _GeneralSettingsService: GeneralSettingsService, private _FlashMessages:FlashMessagesService ,private _FormBuilder: FormBuilder) {this.CreateHeadingForm()}
  ngOnInit(): void {
    this._GeneralSettingsService.GetCardOneImage().subscribe((docs:any)=>{
      if(docs.length >= 1){
      this.ImageUrlOne = this.ProductUrl + docs[0].imageUrl;
      this.CardOneStyle['background-image'] = 'url('+this.ImageUrlOne+')';
      }else{
        return null
      }

    })

    this._GeneralSettingsService.GetCardTwoImage().subscribe((docs:any)=>{
      if(docs.length >= 1){
        this.ImageUrlTwo = this.ProductUrl + docs[0].imageUrl;
        this.CardTwoStyle['background-image'] = 'url('+this.ImageUrlTwo+')';
      }else{
        return null
      }
    })

    this._GeneralSettingsService.GetCardThreeImage().subscribe((docs:any)=>{
      if(docs.length >= 1){
        this.ImageUrlThree = this.ProductUrl + docs[0].imageUrl;
        this.CardThreeStyle['background-image'] = 'url('+this.ImageUrlThree+')';
      }else{
        return null
      }
    })

    this._GeneralSettingsService.GetCardFourImage().subscribe((docs:any)=>{
      if(docs.length >= 1){
        this.ImageUrlFour = this.ProductUrl + docs[0].imageUrl;
        this.CardFourStyle['background-image'] = 'url('+this.ImageUrlFour+')';
      }else{
        return null
      }
    })

    this._GeneralSettingsService.GetFooterImage().subscribe((docs:any)=>{
      let _docs = docs.Result;
      this.FooterImageUrl = this.FooterImageFolderUrl + _docs[0].imageUrl;
      this.FooterStyle['background-image'] = 'url('+this.FooterImageUrl+')';
    })



  }

  CreateHeadingForm() {
    this.HeadingFormData = this._FormBuilder.group({
      Heading: ['', [Validators.required, Validators.minLength(2)]],
      HeadingDescription: ['',[Validators.required,Validators.minLength(2),Validators.maxLength(500)]],
      HeadingCategory: ['', Validators.required]
    });
  }
  
  SubmitHeadingData(){
    console.log('Heading Function Called from component.ts');
    let _HeadingFormData = this.HeadingFormData.value;
    this._GeneralSettingsService.PostHeadingData(_HeadingFormData).subscribe((data:any) =>{
      let Message = data.Message;
      this._FlashMessages.show(Message, { cssClass: 'alert-success', timeout: 10000 })
    });
    this.HeadingFormData.reset();
  }

  GetFooterImage(event){
    this.FooterImageData = event.target.files[0];
  }

  onSubmitFooterImage(){
    const _FooterImageFormData = new FormData();
    _FooterImageFormData.append('FooterImage',this.FooterImageData);
    this._GeneralSettingsService.PostFooterImage(_FooterImageFormData).subscribe((docs:any)=>{
      console.log(docs);
      this.message = docs.Message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-success', timeout: 10000 });
      this.ngOnInit();
    })
    this.FooterImage.nativeElement.value= null;
  }

  GetFiles(event) {
    this.VideoData = event.target.files[0];
  }

  onRegisterSubmit() {
    const _VideoData = new FormData();
    _VideoData.append('video', this.VideoData);
    this._GeneralSettingsService.PostVideo(_VideoData).subscribe((docs:any) => {
      this.message = docs.Message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-success', timeout: 10000 });
    });
    this.FileSelect.nativeElement.value= null;
  }






  //Block Start Product Category Box
  GetCardOne(event){
    this.FirstImage = event.target.files[0];
  }
  GetCardTwo(event){
    this.SecondImage = event.target.files[0];
  }
  GetCardThree(event){
    this.ThirdImage = event.target.files[0];
  }
  GetCardFour(event){
    this.FourthImage = event.target.files[0];
  }

  SubmitFirstCardImage(){
    let _FormDataWithImage = new FormData();
    _FormDataWithImage.append('image',this.FirstImage);
    _FormDataWithImage.append('FolderName','CardOne');
    this._GeneralSettingsService.PostFirstImage(_FormDataWithImage).subscribe((DataComingFromBackEndInResponse:any)=>{
      this.message = DataComingFromBackEndInResponse.Message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-success', timeout: 10000 });
      this.ngOnInit();
    });
    this.CardOne.nativeElement.value= null;
  }
  SubmitSecondCardImage(){
    let _FormDataWithImage = new FormData();
    _FormDataWithImage.append('FolderName','CardTwo');
    _FormDataWithImage.append('image',this.SecondImage);
    this._GeneralSettingsService.PostSecondImage(_FormDataWithImage).subscribe((DataComingFromBackEndInResponse:any)=>{
      this.message = DataComingFromBackEndInResponse.Message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-success', timeout: 10000 });
      this.ngOnInit();
    });
    this.CardTwo.nativeElement.value= null;
  }
  SubmitThirdCardImage(){
    let _FormDataWithImage = new FormData();
    _FormDataWithImage.append('image',this.ThirdImage);
    _FormDataWithImage.append('FolderName','CardThree');
    this._GeneralSettingsService.PostThirdImage(_FormDataWithImage).subscribe((DataComingFromBackEndInResponse:any)=>{
      this.message = DataComingFromBackEndInResponse.Message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-success', timeout: 10000 });
      this.ngOnInit();
    });
    this.CardThree.nativeElement.value= null;
  }
  SubmitFourthCardImage(){
    let _FormDataWithImage = new FormData();
    _FormDataWithImage.append('image',this.FourthImage);
    _FormDataWithImage.append('FolderName','CardFour');
    this._GeneralSettingsService.PostFourthImage(_FormDataWithImage).subscribe((DataComingFromBackEndInResponse:any)=>{
      this.message = DataComingFromBackEndInResponse.Message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-success', timeout: 10000 });
      this.ngOnInit();
    });
    this.CardFour.nativeElement.value= null;
  }
  //Block End Product Category Box
}
