import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms'
import { ECommerceDataServiceService } from '../Shared/Services/e-commerce-data-service.service';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import { SendEmailServiceService } from '../Shared/Services/send-email-service.service';
import { FlashMessagesService } from 'angular2-flash-messages';


@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html',
  styleUrls: ['./contact.component.css']
})
export class ContactComponent implements OnInit {
  
  @ViewChild('video') audio:ElementRef;
  MyForm: FormGroup;
  private Data;
  private FormData;
  WhatsApp: FormGroup;
  private WhatsAppData;
  chatBtn = 1;
  chatBox = 0;
  WhatsAppMsgFromBackend:any;
  constructor(private _FormBuilder:FormBuilder,private _EcommerceService:ECommerceDataServiceService, private _SendEmailService:SendEmailServiceService,private _FlashMessages:FlashMessagesService) {this.MyFormModel(),this.WhatsAppModel()}

  ngOnInit(): void {
  }


  // Starting ChatBox Toggle Code
  chatOpen() {
    this.chatBtn = 0;
    this.chatBox = 1;
  }
  closeChat() {
    this.chatBox = 0;
    this.chatBtn = 1;
    this.WhatsApp.reset();
  }
  // Ending ChatBox Toggle Code


  // Starting Forms and Submiting Forms Data
  MyFormModel(){
    this.MyForm=this._FormBuilder.group({
      name:['', [Validators.required,Validators.minLength(2),Validators.maxLength(50)]],
      instaUserName:['', [Validators.required,Validators.minLength(2),Validators.maxLength(20)]],
      email:['', [Validators.required,Validators.email]],
      WhyDoYouWantToFollowUs:['', [Validators.required,Validators.minLength(2),Validators.maxLength(200)]],
      WhyDoYouWantTOBecomeAnAmbassador:['',[Validators.required,Validators.minLength(2),Validators.maxLength(200)]],
      WhereYouFrom:['',Validators.required]
    })
  }

  WhatsAppModel(){
    this.WhatsApp=this._FormBuilder.group({
      WhatsAppMessage:['', [Validators.required,Validators.minLength(2),Validators.maxLength(50)]],
      PhoneNumber:['', [Validators.required,Validators.pattern(/^[0-9*#+]*$/)]],
      WhatsAppMsgBackend:''
    })
  }
  OnSubmitSendEmail(){
   var EmailFormDataObjectValue=this.MyForm.value;
    this._SendEmailService.SendEmail(EmailFormDataObjectValue).subscribe((DataComingFromBackEndInResponse:any)=>{
      this._FlashMessages.show(DataComingFromBackEndInResponse.Message,{cssClass:'alert-success', timeout:2000});
      this.MyForm.reset();
    });
  }

  SendWhatsAppMessage(){
    this.WhatsAppData=this.WhatsApp.value;
    this._EcommerceService.SendWhatsAppMsg(this.WhatsAppData).subscribe(result=>{this.WhatsAppMsgFromBackend=Object.values(result),this.WhatsApp.controls['WhatsAppMsgBackend'].setValue(this.WhatsAppMsgFromBackend),console.log(this.WhatsAppMsgFromBackend)});
  }
  // Ending Forms and Submiting Forms Data


  //Starting Geo Map Code
  ngAfterViewInit() {
    am4core.useTheme(am4themes_animated);
    // Themes end

    // Create map instance
    let chart = am4core.create("chartdiv", am4maps.MapChart);

    // Set map definition
    chart.geodata = am4geodata_worldLow;

    // Set projection
    chart.projection = new am4maps.projections.Miller();

    // Create map polygon series
    let polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());

    // Exclude Antartica
    polygonSeries.exclude = ["AQ"];

    // Make map load polygon (like country names) data from GeoJSON
    polygonSeries.useGeodata = true;

// Disabling mouse wheel zoom in maps
    	chart.chartContainer.wheelable = false;

    //Disabling Chart Logo
    chart.logo.disabled = true;


    // Configure series
    let polygonTemplate = polygonSeries.mapPolygons.template;
    polygonTemplate.tooltipText = "{name}";
    polygonTemplate.polygon.fillOpacity = 1;
    

    // Create hover state and set alternative fill color
    let hs = polygonTemplate.states.create("hover");
    hs.properties.fill = am4core.color("#5cb85c");

    // Add image series I am implementing the circles on the map
    let imageSeries = chart.series.push(new am4maps.MapImageSeries());
    imageSeries.mapImages.template.propertyFields.longitude = "longitude";
    imageSeries.mapImages.template.propertyFields.latitude = "latitude";
    imageSeries.mapImages.template.tooltipText = "{title}";
    

    let circle = imageSeries.mapImages.template.createChild(am4core.Circle);
    circle.radius = 3;
    circle.propertyFields.fill = "color";

    let circle2 = imageSeries.mapImages.template.createChild(am4core.Circle);
    circle2.radius = 3;
    circle2.propertyFields.fill = "color";

    circle2.events.on("inited", function (event) {
      animateBullet(event.target);
    })

    function animateBullet(circle) {
      let animation = circle.animate([{ property: "scale", from: 1, to: 5 }, { property: "opacity", from: 1, to: 0 }], 1000, am4core.ease.circleOut);
      animation.events.on("animationended", function (event) {
        animateBullet(event.target.object);
      })
    }

    let colorSet = new am4core.ColorSet();

    imageSeries.data = [
      {
        "title": "Washington",
        "latitude": 38.8921,
        "longitude": -77.0241,
        "color": colorSet.next()
      },
      {
        "title": "Pakistan",
        "latitude": 30.00,
        "longitude": 70.00,
        "color": colorSet.next()
      }
    ];
	// Here I am highlighting only those maps which I want to highlights	
    polygonSeries.data = [{
      "id": "US",
      "name": "United States",
      "value": 100,
      "fill": am4core.color("#F05C5C")
    }, {
      "id": "PK",
      "name": "Pakistan",
      "value": 50,
      "fill": am4core.color("#5C5CFF")
    }];
    
    // Bind "fill" property to "fill" key in data
    polygonTemplate.propertyFields.fill = "fill";

    
  }
  //Ending Geo Map Code

  // Custome Mute the video
  Mute(){
    
  }
  // Custome Mute the video
}
