import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AdminManagementService } from 'src/app/Shared/Services/admin-management.service';

@Component({
  selector: 'app-admin-registeration',
  templateUrl: './admin-registeration.component.html',
  styleUrls: ['./admin-registeration.component.css']
})
export class AdminRegisterationComponent implements OnInit {
  AdminLimit=0;
  MyForm: FormGroup;
  ImageUrl: any;
  constructor(private _FormBuilder:FormBuilder, private _FlashMessages:FlashMessagesService, private _Router:Router, private _AdminManagementSerivce:AdminManagementService) { this.MyFormModel()   }

  ngOnInit(): void {
  }
  MyFormModel() {
    this.MyForm = this._FormBuilder.group({
      name: ['', [Validators.required, Validators.minLength(4), Validators.maxLength(50), Validators.pattern(/^[a-zA-Z ]*$/)]],
      email: ['', [Validators.required, Validators.email]],
      mobile: ['', [Validators.required, Validators.minLength(10), Validators.maxLength(15), Validators.pattern(/^[0-9*#+]*$/)]],
      password: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(15)]],
      file: ['', [Validators.required]],
      address: ['', [Validators.required, Validators.minLength(6)]]
    });

  }

  onFileSelect(event) {
    this.ImageUrl=event.target.files[0];
    this.MyForm.get('file').setValue(this.ImageUrl);
    // to display the image on the angular
    // if (event.target.files) {
    //   var reader = new FileReader();
    //   reader.readAsDataURL(event.target.files[0]);
    //   reader.onload = (FinalData: any) => {
    //     this.ImageUrl = FinalData.target.result;
    //   }
    // }

  }

  onRegister() {
    const UserRegisterationData = new FormData();
    UserRegisterationData.append('name',this.MyForm.get('name').value);
    UserRegisterationData.append('email',this.MyForm.get('email').value);
    UserRegisterationData.append('mobile',this.MyForm.get('mobile').value);
    UserRegisterationData.append('password',this.MyForm.get('password').value);
    UserRegisterationData.append('AdminImage',this.MyForm.get('file').value);
    UserRegisterationData.append('address',this.MyForm.get('address').value);
    this._AdminManagementSerivce.AdminRegister(UserRegisterationData).subscribe((DataComingFromBackEndInResponse:any)=>{
      if(DataComingFromBackEndInResponse.Data == 0){
        this.AdminLimit=2;
      }
     this._FlashMessages.show(DataComingFromBackEndInResponse.Message,{cssClass:'alert-success', timeout:7000});
      
    });

    // 1
    // This Snippet is used to playe with User Data without any Server. It is all about Angular Code
    // const MyFromObject=this.MyForm.value;
    // this._AuthService.Register(MyFromObject).subscribe(DataComingFromBackend=>{
    //   this._MessengerService.sendMsg(DataComingFromBackend);
    //   this._MessengerService.SendImageUrl(this.ImageUrl);
    //   this._FlashMessages.show('Register Successfully', {cssClass:'alert-success', timeout:2000});
    //   this._Router.navigate(['/home']);
    // });

    // 2
    // Note to print FormData values use this snippet
    // for(const FormData of DataComingFromBackEndInResponse){
    //   console.log(FormData);
    // }
  }

}
