import { Component, OnInit } from '@angular/core';
import { AdminManagementService } from 'src/app/Shared/Services/admin-management.service';
import { GetAdminImagesUrl } from 'src/configuration/GlobalConstants';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  WebsiteStats:any;
  Date:any;
  constructor(private _AdminManagementService:AdminManagementService) { }

  ngOnInit(): void {
    var AdminIdFrommLocalStorage=localStorage.getItem('admin_Id');
    this._AdminManagementService.GetAdminDataById(AdminIdFrommLocalStorage).subscribe((DataComingFromBackEnd:any)=>{
      this.Date=DataComingFromBackEnd.AdminData.CreatedDate;
    })
  }

}
