import { Component, OnInit } from '@angular/core';
import { AdminManagementService } from 'src/app/Shared/Services/admin-management.service';

@Component({
  selector: 'app-website-analytics',
  templateUrl: './website-analytics.component.html',
  styleUrls: ['./website-analytics.component.css']
})
export class WebsiteAnalyticsComponent implements OnInit {

  Visitors:any;
  Clicks:any;
  TotalPercentage:any;
  constructor(private _AdminManagementService:AdminManagementService) { }

  ngOnInit(): void {
    this._AdminManagementService.GetWebsiteStats().subscribe((DataComingFromBackEnd:any)=>{
      this.Visitors=DataComingFromBackEnd[0].NewVisitors.VisitorCounter;
      this.Clicks=DataComingFromBackEnd[0].ProductClicks.ClicksCounter;
      this.TotalPercentage=(this.Visitors+this.Clicks);
    })
  }

}
