import { dashCaseToCamelCase } from '@angular/compiler/src/util';
import { Component, OnInit } from '@angular/core';
import { AdminManagementService } from 'src/app/Shared/Services/admin-management.service';
import { UserManagementService } from 'src/app/Shared/Services/user-management.service';
import {GetUserImagesUrl} from '../../../../configuration/GlobalConstants';

@Component({
  selector: 'app-user-management',
  templateUrl: './user-management.component.html',
  styleUrls: ['./user-management.component.css']
})
export class UserManagementComponent implements OnInit {
  Message="Please Unspende me";
  loop=10;
  UserData=[];
  Url=GetUserImagesUrl;
  ActiveUsers:any;
  SuspendedUsers=[];
  TotalSuspendedUsers:number;
  SuspendedSandBoxDetails:any;
  SuspendedUSerName:any;
  SuspendedUserMsg:any;
  SandBoxId:any;
  SuspendedUsersIdArray=[];
  SandBox: any;
  constructor(private _AdminManagementService:AdminManagementService,private _UserManagementService:UserManagementService) { }

  ngOnInit(): void {

    //Get ALl Users
    this._AdminManagementService.GetAllUsers().subscribe((DataComingFromBackEnd:any)=>{
      for (let i=0;i<DataComingFromBackEnd.length;i++){
        if(DataComingFromBackEnd[i].Status===0){
          
          this.SuspendedUsers.push(DataComingFromBackEnd[i]);
        }
        else{
          this.UserData.push(DataComingFromBackEnd[i]);
        }
      }
    });
    this.UserData=[];
    this.SuspendedUsers=[];
    //Get ALl Users


    //Getting Website Stats
    this._AdminManagementService.GetWebsiteStats().subscribe((DataComingFromBackEnd:any)=>{
      this.ActiveUsers=DataComingFromBackEnd[0].ActiveUsers.UserCounter;
    })
    //Getting Website Stats


    //Getting All Suspended Users
    this._UserManagementService.GetSuspendedUsers().subscribe((DataComingFromBackEnd:any)=>{
      this.TotalSuspendedUsers=DataComingFromBackEnd.TotalUsers;
      for(let i=0;i<DataComingFromBackEnd.SuspendedUsers.length;i++){
        this.SuspendedUsersIdArray.push(DataComingFromBackEnd.SuspendedUsers[i]._id);
      }
      this._UserManagementService.GetUnSuspensionBody(this.SuspendedUsersIdArray).subscribe((DataComingFromBackEnd:any)=>{
        console.log(DataComingFromBackEnd);
        this.SandBox=DataComingFromBackEnd;
      })
    })
    //Getting All Suspended Users
  }

  SuspendUser(UserId){
    this._UserManagementService.SuspendUser(UserId).subscribe(DataComingFromBackEnd=>{
      this.ngOnInit();
    })
  }

  DeleteUser(UserImageUrl){
    this._UserManagementService.DeleteUser(UserImageUrl).subscribe(DataComingFromBackEnd=>{
      this._UserManagementService.DecrementActiveUsers().subscribe(DataComingFromBackEndForStats=>{this.ngOnInit();});
      this.ngOnInit();
    })
  }

  UnSuspendUser(UserId,SandBoxId){
    console.log(UserId);
    console.log(SandBoxId);
    this._UserManagementService.UnSuspendUser(UserId).subscribe(DataComingFromBackENd=>{
      this.ngOnInit();
    })
    this._UserManagementService.DeleteUnSuepensionBody(SandBoxId).subscribe(DataComingFromBackEnd=>{
      this.ngOnInit();
    });
  }

}
