import { object } from '@amcharts/amcharts4/core';
import { Component, OnInit, AfterViewChecked, ViewChild, ElementRef, NgZone } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { AddToCartService } from 'src/app/Shared/Services/add-to-cart.service';
import { ECommerceDataServiceService } from 'src/app/Shared/Services/e-commerce-data-service.service';
import { CheckOutService } from '../../Shared/Services/check-out.service';

declare var paypal;
@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {
  @ViewChild('paypal', { static: true }) paypalElement: ElementRef;

  _CheckOutForm: FormGroup;
  _Cart: any = [];
  CartTotal: any;
  TotalItems: any;
  _ShipmentCharges: any = [];
  _Total = 0;
  _Data: any;
  _CartCheckOutData: any = [];
  _ApprovedPayPalUrl: any;
  _ToUpdateQuantityAfterCheckOut:any =[];
  product = {
    description: 'E-Commerce Web AppParcel',
  };
  paidFor = false;

  CustomStyle = {
    display: 'none',
    'justify-content': 'center'
  };

  _Check: any;

  constructor(private _EcommerceDataService:ECommerceDataServiceService,private _NgZone: NgZone, private _FormBuilder: FormBuilder, private _Router: Router, private _AddToCartService: AddToCartService, private _CheckOutService: CheckOutService) { this.CheckOutFormModel() }

  ngOnInit(): void {
    let _CartData = this._AddToCartService.GetCartFromLocalStorage();
    if (_CartData) {
      let _CartTotal = this._AddToCartService.GetCartTotal();
      let _TotalItems = this._AddToCartService.GetNumberOfTotalCartItems();
      this.CartTotal = _CartTotal;
      this.TotalItems = _TotalItems;
      this._Cart = { CartData: _CartData, CartTotal: _CartTotal, TotalItems: _TotalItems };
      this._Check = true;
    } else {
      this._Check = false;
    }
  }

  RenderPaypalButton() {
    paypal
      .Buttons({
        createOrder: async (data, actions) => {
          return await actions.order.create({
            purchase_units: [
              {
                description: this.product.description,
                amount: {
                  currency_code: 'USD',
                  value: this._Total
                }
              }
            ]
          });
        },
        onApprove: async (data, actions) => {
          // const order = await actions.order.capture();
          // this.paidFor = true;
          // this.CheckOut();
          // this.CustomStyle['display'] = 'none';
          const order = await actions.order.capture();
          if(order.status === `COMPLETED`){
            this.paidFor = true;
          this._NgZone.run(() => {
            this.CheckOut();
            this._Router.navigate(['ThankYou']);
          })
          }else{
            return null;
          }
          
        },
        onError: err => {
          console.log(err);
        }
      })
      .render(this.paypalElement.nativeElement);
  }

  ViewCart() {
    this._Router.navigate(['product']);
  }

  CheckOutFormModel() {
    this._CheckOutForm = this._FormBuilder.group({
      name: ['', [Validators.required, Validators.pattern(/^[a-zA-Z ]*$/)]],
      street: ['', [Validators.required]],
      city: ['', [Validators.required, Validators.pattern(/^[a-zA-Z ]*$/)]],
      state: ['', [Validators.required]],
      zip: ['', [Validators.required, Validators.pattern(/^[0-9*#+]*$/)]],
      ShipmentMethod: ['', Validators.required],
      EmailOrMobile: ['', [Validators.required, Validators.minLength(5), Validators.maxLength(50)]]
    });
  }

  GetShipmentRates() {
    let CheckOutFormValue = this._CheckOutForm.value;
    this._CheckOutService.GetRates(CheckOutFormValue).subscribe((DataComingFromBackend: any) => {
      if (DataComingFromBackend.Data === true) {
        DataComingFromBackend.Result.map(_Data => {
          this._ShipmentCharges.push(_Data);
          this.CustomStyle['display'] = 'flex';
        })
      } else {
        return null;
      }

    })
  }

  FinalRates(event, data) {
    // if (event.target.checked) {
    //   let _value = data;
    //   this._Data.push(_value);
    // } else {
    //   this._Data = this._Data.filter(value => value.Service != data.Service);
    //   console.log(this._Data);
    // }
    this._Data = data;
    this._CheckOutService.SetLocalStorageForShipmentCharges(data.Rates);
    let _ShipmentRates = this._CheckOutService.GetLocalStorageForShipmentCharges();
    this._Total = parseFloat(this.CartTotal) + parseFloat(_ShipmentRates);
  }

  CheckOut() {
    const _CheckOutData = new FormData(); // Creating FormData object to send mulitpart data
    _CheckOutData.append('Name', this._CheckOutForm.get('name').value);//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('Street', this._CheckOutForm.get('street').value);//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('City', this._CheckOutForm.get('city').value);//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('State', this._CheckOutForm.get('state').value);//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('Zip', this._CheckOutForm.get('zip').value);//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('ShipmentMethod', this._CheckOutForm.get('ShipmentMethod').value)//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('EmailOrMobile', this._CheckOutForm.get('EmailOrMobile').value)//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('ShipmentRate', this._Data.Rates)//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('ShipmentDays', this._Data.Days)//Appending values to the _CheckOutForm varibale from FormGroup
    _CheckOutData.append('ShipmentService', this._Data.Service)//Appending values to the _CheckOutForm varibale from FormGroup



    // _CheckOutData.forEach((key,value)=>{
    //   let Value=key;
    //   let Key=value;
    //   let TemporaryCart={
    //   }
    //   TemporaryCart['Value']=Key;
    //   this._CartCheckOutData.push(TemporaryCart);
    // })

    _CheckOutData.forEach(function (value, key) {
      object[key] = value;
    });
    let _OrderDetails = this._AddToCartService.GetCartFromLocalStorage();
    let _CartTotal = this._AddToCartService.GetCartTotal();
    let _TotalCartItems = this._AddToCartService.GetNumberOfTotalCartItems();
    let _SenderDetails = object;
    let _CompleteOrder = {
      SenderDetails: _SenderDetails,
      OrderDetails: _OrderDetails,
      TotalItems: _TotalCartItems,
      CartTotal: _CartTotal,
      CartTotalIncludingShipmentCharges: this._Total
    }
    _OrderDetails.map(data=>{
      this._ToUpdateQuantityAfterCheckOut.push({
        ProductCategory:data.ProductCategory,
        ProductQuantity:data.qty, 
        ProductId:data.productId
      })
    })
    this._CheckOutService.SetCheckOut(_CompleteOrder);
    this._EcommerceDataService.SetOrderToDatabase(_CompleteOrder).subscribe(DataComingFromBackend=>{
    })
    // this._CheckOutService.PostPayPalPayment().subscribe((DatacomingFrombackend: any) => {
    //   this._ApprovedPayPalUrl = DatacomingFrombackend.Url;
    //   window.open(this._ApprovedPayPalUrl);
    // })
    this._CheckOutForm.reset();
    this._ShipmentCharges = [];
    this._Data = [];
    this._CheckOutService.ClearLocalStorageExceptCheckOutData();
  }

  GotToEditCart() {
    this._Router.navigate(['ViewCart']);
  }


}
