import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-line-chart',
  templateUrl: './line-chart.component.html',
  styleUrls: ['./line-chart.component.css']
})
export class LineChartComponent implements OnInit {

  line1Values=[];
  TotalLine1Values:number=0;
  line1Percentage:number;
  line1Color:string;

  public type="line";
  public data=[{data:[65,59,80,81,56,55,40],label:'Sales'}];
  public labels=['jan','feb','mar','apr','may','jun','jul','aug','sep','oct','nov','dec'];
  public colors=[
    {backgroundColor: 'rgb(41, 43, 44,0.7)',borderColor: 'rgb(0,0,0)',borderWidth: 3,}
  ];
  public options={
    scaleShowVerticalLines:false,
    responsive:true,
  };

  constructor() { }

  ngOnInit(): void {
    this.line1Color=this.colors[0].backgroundColor;
    for(let i=0;i<this.data[0].data.length;i++){
      this.line1Values.push(this.data[0].data[i]);
      this.TotalLine1Values+=this.line1Values[i];
    }
    this.line1Percentage=parseFloat(((this.TotalLine1Values/700)*100).toFixed(2));
  }

}
