import { Component, OnInit } from '@angular/core';
import { ECommerceDataServiceService } from 'src/app/Shared/Services/e-commerce-data-service.service';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css']
})
export class SettingsComponent implements OnInit {
  _Check: boolean;
  _ProductDetails: any=[];
  _SenderDetails: any;
  _TotalOrderLength: any;
  _TotalOrders: any=[];

  constructor(private _ECommerceDataService:ECommerceDataServiceService) { }

  ngOnInit(): void {
    this._ECommerceDataService.GetOrderFromDataBase().subscribe((DataComingFromBackEnd:any)=>{
      if (DataComingFromBackEnd.Result.length>0) {
        console.log(DataComingFromBackEnd);
        this._TotalOrderLength = DataComingFromBackEnd.Result;
        this._Check = true;
        DataComingFromBackEnd.Result.map(values=>{
          this._TotalOrders.push(values);
          console.log(this._TotalOrders);
        })
      } else {
        this._Check = false;
      }
    })
  }

}
