import { Component, OnInit } from '@angular/core';
import { ECommerceDataServiceService } from '../Shared/Services/e-commerce-data-service.service';
import { GeneralSettingsService } from '../Shared/Services/general-settings.service';
import { GetProductCategoryImagesUrl } from '../../configuration/GlobalConstants';
import {GetFooterImageFolderUrl} from '../../configuration/GlobalConstants';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  UrlTwo: any;
  UrlThree: any;
  UrlFour: any;
  Url: any;
  FooterImageFolderUrl =  GetFooterImageFolderUrl;
  _HeadingData: any = [];
  _HeadingOneHead:any;
  _HeadingTwoHead:any;
  _HeadingThreeHead:any;
  _HeadingFourHead:any;
  _HeadingOneDescription:any;
  _HeadingTwoDescription:any;
  _HeadingThreeDescription:any;
  _HeadingFourDescription:any;
  _Flag:any;
  FooterStyle={
    'background-image':'',
    'background-repeat':'no-repeat',
    'background-size': 'cover',
    'background-attachment': 'fixed',
    'background-position': 'top',
  }
  None={};
  attachmentList: any = [];
  FooterImageUrl: any;

  constructor(private _EcommerceService: ECommerceDataServiceService, private _GeneralSettingsService: GeneralSettingsService) { }

  ngOnInit(): void {
    this._EcommerceService.GetFeatureCards().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        this.attachmentList.push(data[i]);
      }
    });

    this._GeneralSettingsService.GetCardOneImage().subscribe((docs:any) => {
      if (docs.length>0) {
        this.Url = GetProductCategoryImagesUrl + docs[0].imageUrl;
      } else {
        return null;
      }
    })

    this._GeneralSettingsService.GetCardTwoImage().subscribe((docs:any) => {
      if (docs.length>0) {
        this.UrlTwo = GetProductCategoryImagesUrl + docs[0].imageUrl;
      } else {
        return null;
      }
    })

    this._GeneralSettingsService.GetCardThreeImage().subscribe((docs:any) => {
      if (docs.length>0) {
        this.UrlThree = GetProductCategoryImagesUrl + docs[0].imageUrl;
      } else {
        return null;
      }
    })

    this._GeneralSettingsService.GetCardFourImage().subscribe((docs:any) => {
      if (docs.length>0) {
        this.UrlFour = GetProductCategoryImagesUrl + docs[0].imageUrl;
      } else {
        return null;
      }
    })

    this._GeneralSettingsService.GetHeadingData().subscribe((docs: any) => {
      for (let i = 0; i < docs.Result.length; i++){
        this._HeadingData.push(docs.Result[i]);
      }
      this._HeadingData.map((docs,iterator) => {
        if(docs.HeadingCategory === 'Heading 1'){
          this._HeadingOneHead = docs.Heading;
          this._HeadingOneDescription = docs.HeadingDescription;
        }else if(docs.HeadingCategory === 'Heading 2'){
          this._HeadingTwoHead = docs.Heading;
          this._HeadingTwoDescription = docs.HeadingDescription;
        }else if(docs.HeadingCategory === 'Heading 3'){
          this._HeadingThreeHead = docs.Heading;
          this._HeadingThreeDescription = docs.HeadingDescription;
        }else if(docs.HeadingCategory === 'Heading 4'){
          this._HeadingFourHead = docs.Heading;
          this._HeadingFourDescription = docs.HeadingDescription;
        }
      })
    })

    this._GeneralSettingsService.GetFooterImage().subscribe((docs:any)=>{
      let _docs = docs.Result;
      if(!(_docs.length==0)){
      this.FooterImageUrl = this.FooterImageFolderUrl + _docs[0].imageUrl;
      this.FooterStyle['background-image'] = 'url('+this.FooterImageUrl+')';
      this._Flag=1;
      }else{
        this._Flag=0;
      }
    })
  }

}


