import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class AddToCartService {

  constructor() { }

  SetCartToLocalStorage(Cart){
    localStorage.setItem('UserCart',JSON.stringify(Cart));
  }

  GetCartFromLocalStorage(){
    return JSON.parse(localStorage.getItem('UserCart'));
  }

  SetCartTotal(CartTotal){
    localStorage.setItem('CartTotal',JSON.stringify(CartTotal));
  }

  GetCartTotal(){
    return JSON.parse(localStorage.getItem('CartTotal'));
  }

  SetNumberOfTotalCartItems(TotalCart){
    localStorage.setItem('NumberOfTotalCartItems',JSON.stringify(TotalCart));
  }

  GetNumberOfTotalCartItems(){
    return JSON.parse(localStorage.getItem('NumberOfTotalCartItems'));
  }
  ClearCartFromLocalStorage(){
    localStorage.removeItem('UserCart');
    localStorage.removeItem('CartTotal')
  }
}
