import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpClientModule } from '@angular/common/http';
import { PostCardUrl, PostCardWithMultipleImagesUrl } from '../../../configuration/GlobalConstants';
import { GetCardWithMultipleImagesUrl } from '../../../configuration/GlobalConstants';
import { DeleteCardUrl } from '../../../configuration/GlobalConstants';
import { PostCartItemsUrl } from '../../../configuration/GlobalConstants';
import { ManageQuantityUrl } from '../../../configuration/GlobalConstants';
import { GetProductById } from '../../../configuration/GlobalConstants'
import { SendWhatsAppMessage, GetCartItemsUrl } from '../../../configuration/GlobalConstants';
import { UpdateQuantityByIdUrl } from '../../../configuration/GlobalConstants';
import { PostFeatureCardUrl } from '../../../configuration/GlobalConstants';
import { GettFeatureCardUrl } from '../../../configuration/GlobalConstants';
import { DeleteFeatureCardUrl } from '../../../configuration/GlobalConstants';
import { GetFeatureCardByIdUrl } from '../../../configuration/GlobalConstants';
import {
  UpdateFeatureCardByIdUrl,
  PostSizeUrl,
  GetCardDataForViewCartCompnentUrl,
  PostOrderUrl,
  GetOrderUrl
} from '../../../configuration/GlobalConstants';
import { of } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class ECommerceDataServiceService {




  constructor(private _HttpClient: HttpClient) { }

  //Starting Feature Cards Apis Methods
  UpdateFeatureCardQuantityById(id, NewData) {
    return this._HttpClient.put(UpdateFeatureCardByIdUrl + `${id}`, NewData);
  }
  GetFeatureProductsById(id) {
    return this._HttpClient.get(GetFeatureCardByIdUrl + `${id}`);
  }
  CreateFeatureCard(FeatureCardData) {
    return this._HttpClient.post(PostFeatureCardUrl, FeatureCardData);
  }
  GetFeatureCards() {
    return this._HttpClient.get(GettFeatureCardUrl);
  }
  DeleteFeatureCard(imageUrl) {
    return this._HttpClient.delete(DeleteFeatureCardUrl + `${imageUrl}`);
  }
  //Ending Feature Cards Apis Methods

  //Function to Update Data by particular Id
  UpdateQuantityById(id, NewData) {
    return this._HttpClient.put(UpdateQuantityByIdUrl + `${id}`, NewData);
  }
  //Ending Function to Update Data by particular Id


  //Function to get Data by particular Id
  GetProductsById(id) {
    return this._HttpClient.get(GetProductById + `${id}`);
  }
  //Ending Function to get Data by particular Id

  //Function to send Data to Backend
  createCard(cardData) {
    // return this._HttpClient.post(PostCardWithMultipleImagesUrl, cardData);
  }
  // Ending Function to send Data to Backend

  //Function to Send Card With Multiple Images
  CreatCardWithMultipleData(cardData) {
    return this._HttpClient.post(PostCardWithMultipleImagesUrl, cardData);
  }
  //Ending Function to Send Card With Multiple Images

  //Block Start: Get Card with Multiple Images
  GetCardWithMultipleImages() {
    return this._HttpClient.get(GetCardWithMultipleImagesUrl);
  }
  //Block Ends: Get Card with Multiple Images

  //Funtion to Get all Data
  getCards() {
    return this._HttpClient.get(GetCardWithMultipleImagesUrl);
  }
  //Ending Funtion to Get all Data

  //Function to Delete Cards from Db and from Folder
  deleteCards(ProductName) {
    return this._HttpClient.delete(DeleteCardUrl + `${ProductName}`);
  }

  //Ending Function to Delete Cards from Db and from Folder



  // Managing Quantities 

  ManageQuantity(NameOfProduct: any) {
    return this._HttpClient.get(ManageQuantityUrl + `${NameOfProduct}`);
  }

  //Ending Managing Quantities 

  //Sending Message to WhatsApp

  SendWhatsAppMsg(data) {
    return this._HttpClient.post(SendWhatsAppMessage, data);
  }

  //Ending Sending Message to WhatsApp

  //Managing CartItems
  //Function to Fill Cart with CartItems
  AddToCart(dataComingFromProductList: any) {
    return this._HttpClient.post(PostCartItemsUrl, dataComingFromProductList);
  }

  //Ending Function to Fill Cart with CartItems
  GetCartItems() {
    return this._HttpClient.get(GetCartItemsUrl);
  }
  //ENding CartItemss

  //Block Start To Get All Sizes
  GetSizes(data) {
    return this._HttpClient.post(PostSizeUrl, data);
  }
  //Block Ends To Get All Sizes

  //Block Start Setting/Getting Local Storage To Save Product Id and Then Get all images in view Product Component
  SetLocalStorageForProductData(_Data){
    localStorage.setItem('PI',JSON.stringify(_Data))
  }

  GetLocalStorageForProductData(){
    return JSON.parse(localStorage.getItem('PI'));
  }
  //Block Ends Setting/Getting Local Storage To Save Product Id and Then Get all images in view Product Component


  //Block Start Get Product Card Data with Multiple Images For View Component
  GetCardDataForViewComponent(_ID){
    return this._HttpClient.get(GetCardDataForViewCartCompnentUrl+`${_ID}`);
  }
  //Block End Get Product Card Data with Multiple Images For View Component

  //Block Start for Order Management
  SetOrderToDatabase(OrderObject){
    return this._HttpClient.post(PostOrderUrl,OrderObject);
  }

  GetOrderFromDataBase(){
    return this._HttpClient.get(GetOrderUrl);
  }
  //Block End for Order Management
}

