import { Component, OnInit,Input } from '@angular/core';
import { Router } from '@angular/router';
import { MessengerService } from 'src/app/Shared/Services/messenger.service';
import { GetFeatureCardDirectoryUrl } from '../../../configuration/GlobalConstants';
@Component({
  selector: 'app-feature-produts',
  templateUrl: './feature-produts.component.html',
  styleUrls: ['./feature-produts.component.css']
})
export class FeatureProdutsComponent implements OnInit {
  @Input() FeatureProductData:any;
  Url = GetFeatureCardDirectoryUrl;
  imageUrl: any;
  _ProductQuantity:any;
  constructor(private _Router:Router,private _MessengerService:MessengerService) { }

  ngOnInit(): void {
    this.FeatureProductData.imgDetails.map(docs=>{
      this.imageUrl = docs.imageUrl;
    })
  }

  SendProductId(_ProductId){
    this._MessengerService.SendMessageWithData({
      id:_ProductId,
      Component:'FeatureProductComponent'
    });
    this._Router.navigate(['viewProduct']);
  }

}
