import {Component, OnInit} from '@angular/core';
import {Router} from '@angular/router';
import {ViewportScroller} from '@angular/common';
import {MessengerService} from '../Shared/Services/messenger.service';
import {GeneralSettingsService} from '../Shared/Services/general-settings.service';
import {GetVideoFolderUrl} from '../../configuration/GlobalConstants';
import { AddToCartService } from '../Shared/Services/add-to-cart.service';


@Component({selector: 'app-header', templateUrl: './header.component.html', styleUrls: ['./header.component.css']})
export class HeaderComponent implements OnInit {
    Flag:Number;
    public Url: any;
    Data : any = [];
    IconIncrement:any;
    cartItems:any=[];
    cartTotal = 0;
    style = {
        width: '350px',
        display: 'block',
        backgroundColor: 'black',
        transition: '.3s ease'
      }
    constructor(private _router : Router, private _ViewportScroller : ViewportScroller, private _AddToCartService:AddToCartService, private _GeneralSettingsService : GeneralSettingsService) {}
        
    ngOnInit(): void {
        // this._MessengerService.GetMessageWithData().subscribe(DataComingFromBackend => {});
        this.IconIncrement = this._AddToCartService.GetNumberOfTotalCartItems();
        this.cartItems = this._AddToCartService.GetCartFromLocalStorage();
        this.cartTotal = this._AddToCartService.GetCartTotal();
        this.ChangeVideo();
    }

    async ChangeVideo (){
        await this._GeneralSettingsService.GetVideo().subscribe((DataComingFromBackend:any) => {
            if(DataComingFromBackend.Status===1){
            this.Url=GetVideoFolderUrl+DataComingFromBackend.Data[0].videoUrl;
            this.Flag=1;
            } else{
              this.Flag=0;
            }
        })
    }

    goHome() {
        this._router.navigate(['home']);
        this._ViewportScroller.scrollToAnchor('jump');

    }
    goProducts() {
        this._router.navigate(['product']);
        this._ViewportScroller.scrollToAnchor('jump');
    }
    goContactUs() {
        this._router.navigate(['contact']);
        this._ViewportScroller.scrollToAnchor('jump');
    }
    goAbout() {
        this._router.navigate(['about']);
        this._ViewportScroller.scrollToAnchor('jump');
    }
    goLogin() {
        this._router.navigate(['login']);
        this._ViewportScroller.scrollToAnchor('jump');
    }
    goToCheckOut(){
        this._router.navigate(['CheckOut']);
        this._ViewportScroller.scrollToAnchor('jump');
    }
    UpdateQuantity(){
        this.ngOnInit();
    }
}

