
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {EasyPostUrl,
        PayPalUrl
} from '../../../configuration/GlobalConstants';
@Injectable({
  providedIn: 'root'
})
export class CheckOutService {
  constructor(private _HttpClient:HttpClient,private _Router:Router) { }

  GetRates(data){
    return this._HttpClient.post(EasyPostUrl,data);
  }

  
  SetLocalStorageForShipmentCharges(ShipmentCharges) {
    localStorage.setItem('ShipmentCharges',JSON.stringify(ShipmentCharges));
  }
  GetLocalStorageForShipmentCharges(){
    return JSON.parse(localStorage.getItem('ShipmentCharges'));
  }

  
  SetCheckOut(_CheckOutData) {
    localStorage.setItem('_CheckOutData',JSON.stringify(_CheckOutData));
  }

  GetCheckOut(){
    return JSON.parse(localStorage.getItem('_CheckOutData'));
  }

  PostPayPalPayment(){
    return this._HttpClient.post(PayPalUrl,'hi');
  }

  ClearLocalStorageExceptCheckOutData(){
    localStorage.removeItem('PI');
    localStorage.removeItem('UserCart');
    localStorage.removeItem('CartTotal');
    localStorage.removeItem('NumberOfTotalCartItems');
    localStorage.removeItem('ShipmentCharges');
  }

  ClearCheckOutData(){
    localStorage.removeItem('_CheckOutData');
  }

  ClearPayPalStorage(){
    localStorage.removeItem('__paypal_storage__');
  }
}
