import { Component, OnInit } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AboutUsSettingsService } from '../Shared/Services/about-us-settings.service';
import { GetAboutUsComponentImagesFolder, GetAboutUsComponentTeamFolder } from '../../configuration/GlobalConstants';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html',
  styleUrls: ['./about.component.css']
})
export class AboutComponent implements OnInit {
  ImageOne: any;
  ImageTwo: any;
  ImageThree: any;
  _HeadingData: any = [];
  _HeadingOneHead: any;
  _HeadingOneDescription: any;
  _HeadingTwoHead: any;
  _HeadingTwoDescription: any;
  _HeadingThreeHead: any;
  _HeadingThreeDescription: any;
  _MemberTwoName:any;
  _MemberThreeName:any;
  _MemberFourName:any;
  _MemberOneName: any;
  _MemberOneRole: any;
  _MemberOneImage: any;
  _MemberTwoRole: any;
  _MemberTwoImage: string;
  _MemberThreeImage: string;
  _MemberThreeRole: any;
  _MemberFourRole: any;
  _MemberFourImage: string;
  _FlagOne=0;
  _FlagTwo=0;
  _FlagThree=0;
  _FlagFour=0;
  constructor(private _AboutUsSettingsService: AboutUsSettingsService, private _FlashMessages: FlashMessagesService) { }

  ngOnInit(): void {
    this._AboutUsSettingsService.GetAboutUsComponentImages().subscribe((docs: any) => {
      let _Data = docs.Result;
      _Data.map((docs, iterator) => {
        if (docs.imageType === 'Image 1') {
          this.ImageOne = GetAboutUsComponentImagesFolder + docs.imageUrl;
        } else if (docs.imageType === 'Image 2') {
          this.ImageTwo = GetAboutUsComponentImagesFolder + docs.imageUrl;
        } else if (docs.imageType === 'Image 3') {
          this.ImageThree = GetAboutUsComponentImagesFolder + docs.imageUrl;
        }
      })
      // this._FlashMessages.show(Message, { cssClass: 'alert-success', timeout: 10000 })
    })

    this._AboutUsSettingsService.GetAboutUsComponentHeadings().subscribe((docs:any)=>{
      for (let i = 0; i < docs.Result.length; i++){
        this._HeadingData.push(docs.Result[i]);
      }
      this._HeadingData.map((docs,iterator) => {
        if(docs.HeadingCategory === 'Heading 1'){
          this._HeadingOneHead = docs.Heading;
          this._HeadingOneDescription = docs.HeadingDescription;
        }else if(docs.HeadingCategory === 'Heading 2'){
          this._HeadingTwoHead = docs.Heading;
          this._HeadingTwoDescription = docs.HeadingDescription;
        }else if(docs.HeadingCategory === 'Heading 3'){
          this._HeadingThreeHead = docs.Heading;
          this._HeadingThreeDescription = docs.HeadingDescription;
        }
      })
    })

    this._AboutUsSettingsService.GetAboutUsComponentTeam().subscribe((docs:any)=>{
      let _Data = docs.Result;
      _Data.map((docs,iterator)=>{
        if(docs.MemberCategory === 'Member 1'){
          this._MemberOneName = docs.Name;
          this._MemberOneRole = docs.Role;
          this._MemberOneImage =GetAboutUsComponentTeamFolder + docs.imageUrl;
          this._FlagOne=1;
        }else if(docs.MemberCategory === 'Member 2'){
          this._MemberTwoName = docs.Name;
          this._MemberTwoRole = docs.Role;
          this._MemberTwoImage =GetAboutUsComponentTeamFolder + docs.imageUrl;
          this._FlagTwo=1;
        }else if(docs.MemberCategory === 'Member 3'){
          this._MemberThreeName = docs.Name;
          this._MemberThreeRole = docs.Role;
          this._MemberThreeImage =GetAboutUsComponentTeamFolder + docs.imageUrl;
          this._FlagThree=1;
        }else if(docs.MemberCategory === 'Member 4'){
          this._MemberFourName = docs.Name;
          this._MemberFourRole = docs.Role;
          this._MemberFourImage =GetAboutUsComponentTeamFolder + docs.imageUrl;
          this._FlagFour=1;
        }
      })
    })
  }

}
