import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { AdminManagementService } from 'src/app/Shared/Services/admin-management.service';
import { MessengerService } from 'src/app/Shared/Services/messenger.service';


@Component({
  selector: 'app-admin-login',
  templateUrl: './admin-login.component.html',
  styleUrls: ['./admin-login.component.css']
})
export class AdminLoginComponent implements OnInit {
  MyForm: FormGroup;
  constructor(private _FormBuilder: FormBuilder, private _Router: Router, private _AdminManagementService: AdminManagementService, private _FlashMessages:FlashMessagesService,private _MessengerService:MessengerService) { this.MyFormModel() }

  ngOnInit(): void {
  }

  // Starting Form and From Submission COde
  MyFormModel() {
    this.MyForm = this._FormBuilder.group({
      email: ['', [Validators.required, Validators.email]],
      password: ['', [Validators.required, Validators.minLength(6), Validators.maxLength(15)]],
    });
  }

  Login() {
    var LoginFormValues = this.MyForm.value;
    this._AdminManagementService.AdminLogin(LoginFormValues).subscribe((DataComingFromBackEnd:any) => {
      if (DataComingFromBackEnd.Data!=null) {
        this._AdminManagementService.LogIn(DataComingFromBackEnd);
      }
      else {
        this._FlashMessages.show(DataComingFromBackEnd.Message,{cssClass:'alert-danger', timeout:7000});
      }
    })
  }

  // Ending Form and From Submission COde
}
