import { Injectable } from '@angular/core';
import {of, ReplaySubject, Subject} from 'rxjs';
import { ECommerceDataServiceService } from './e-commerce-data-service.service';

@Injectable({
  providedIn: 'root'
})
export class MessengerService {
  _Subject = new ReplaySubject(1);
  constructor( private _EcommerceDataService: ECommerceDataServiceService) { }

  SendMessageWithData(MessageAndData){
    this._Subject.next(MessageAndData);
  }
  GetMessageWithData(){
      return this._Subject.asObservable();
  }
  
}
