import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ECommerceDataServiceService } from 'src/app/Shared/Services/e-commerce-data-service.service';
import { GetFilesUrl } from 'src/configuration/GlobalConstants';

@Component({
  selector: 'app-card-analytics',
  templateUrl: './card-analytics.component.html',
  styleUrls: ['./card-analytics.component.css']
})
export class CardAnalyticsComponent implements OnInit {
  //Class Properties
  UpdateMessage:any;
  ID:any;
  UpdateData:FormGroup;
  Quantity: any;
  ProductOutOfStock: any = [];
  attachmentList: any = [];
  ImageData: any=[];
  message: string;
  Url = GetFilesUrl;
  Name: any;
  ImageDataOutOfStock: any=[];
  //Class Properties

  constructor(private _Router: Router, private _FormBuilder: FormBuilder, private _FlashMessages: FlashMessagesService, private _ECommerceService: ECommerceDataServiceService) {this.createFormToUpdateValue()}

  ngOnInit(): void {
    //Here I am Getting cards and then get quantity and push it into ProductsOutOfStock Variable. Actually
    //I am getting array of product where i heck those card whose quantity become zeor and store them into ProductsOutOfStock Variable.
    this._ECommerceService.getCards().subscribe((data: any) => {
      for (let i = 0; i < data.length; i++) {
        if (data[i].quantity === 0) {
          this.ProductOutOfStock.push(data[i]);
          this.ImageDataOutOfStock.push(data[i].imgDetails[0].imageUrl);
        }
        else {
          this.attachmentList.push(data[i]);
          this.ImageData.push(data[i].imgDetails[0].imageUrl);
        }
      }
    });
    this.attachmentList=[];
    this.ProductOutOfStock=[];
    //Here I am Getting cards and then get quantity and push it into ProductsOutOfStock Variable. Actually
    //I am getting array of product where i heck those card whose quantity become zeor and store them into ProductsOutOfStock Variable.
  }

  
   //Funtions of Make and Delete Cards
  DeleteCard(ProductName) {
    this._ECommerceService.deleteCards(ProductName).subscribe((response: any) => {
      this.message = response.message;
      this._FlashMessages.show(this.message, { cssClass: 'alert-danger', timeout: 22000 });
      this.ngOnInit();
    })
  }
 //Funtions of Make and Delete Cards

//Getting the paticular Data from Cards Array using Id So we can Update it
  GetDataForId(id) {
    this.ID=id;
    this._ECommerceService.GetProductsById(id).subscribe((Data: any) => {
      this.Quantity = Data.Result.quantity;
      this.Name = Data.Result.productName;
      this.UpdateData.reset();
    })
  }
//Getting the paticular Data from Cards Array using Id So we can Update it

   //Update Form
  createFormToUpdateValue(){
    this.UpdateData=this._FormBuilder.group({
      UpdateQuantity:'',
    })
  }
//Update Form

    //Update The Quantity
  UpdateTheQuantity() { 
    const data=this.UpdateData.value;
    if (data.UpdateQuantity<=0) {
      this.UpdateMessage="Quantity cant be 0 or less than 0";
    } else {
      this._ECommerceService.UpdateQuantityById(this.ID,data).subscribe((DataComingFromBackEnd:any)=>{
        this.UpdateMessage=DataComingFromBackEnd.message;
        this.ngOnInit();
      })
    }
  }
   //Update The Quantity


}
