import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FlashMessagesService } from 'angular2-flash-messages';
import { FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {AboutUsSettingsService} from '.././../../../Shared/Services/about-us-settings.service';

@Component({
  selector: 'app-about-us-component-settings',
  templateUrl: './about-us-component-settings.component.html',
  styleUrls: ['./about-us-component-settings.component.css']
})
export class AboutUsComponentSettingsComponent implements OnInit {
  @ViewChild('ImageField') ImageField: ElementRef;
  @ViewChild('MemberImageField') MemberImageField: ElementRef;
  ImageFormData: FormGroup;
  ImageData: any;
  HeadingFormData: FormGroup;
  TeamFormData: FormGroup;
  MemberImageData: any;

  constructor(private _FlashMessages:FlashMessagesService, private _FormBuilder: FormBuilder, private _AboutUsSettingsService: AboutUsSettingsService) { this.CreateImageForm(); this.CreateHeadingForm(); this.CreateTeamForm() }

  ngOnInit(): void {
  }

  CreateHeadingForm() {
    this.HeadingFormData = this._FormBuilder.group({
      Heading: ['', [Validators.required, Validators.minLength(2)]],
      HeadingDescription: ['',[Validators.required,Validators.minLength(2),Validators.maxLength(500)]],
      HeadingCategory: ['', Validators.required]
    });
  }

  SubmitHeadingData(){
    console.log('Heading Function Called from component.ts');
    let _HeadingFormData = this.HeadingFormData.value;
    console.log(_HeadingFormData)
    this._AboutUsSettingsService.PostAboutUsComponentHeadings(_HeadingFormData).subscribe((data:any) =>{
      let Message = data.Message;
      this._FlashMessages.show(Message, { cssClass: 'alert-success', timeout: 10000 })
    });
    this.HeadingFormData.reset();
  }

  CreateImageForm() {
    this.ImageFormData = this._FormBuilder.group({
      ImageType: ['', Validators.required]
    });
  }

  GetImage(event) {
    this.ImageData = event.target.files[0];
  }

  SubmitImageFormData(){
    let _FormData = new FormData();
    _FormData.append('ImageType',this.ImageFormData.get('ImageType').value);
    _FormData.append('Image',this.ImageData);
    this._AboutUsSettingsService.SendAboutUsComponentImages(_FormData).subscribe((docs:any)=>{
      console.log(docs);
      let Message = docs.Message;
      this._FlashMessages.show(Message, { cssClass: 'alert-success', timeout: 10000 });
    })
    this.ImageField.nativeElement.value= null;
    this.ImageFormData.reset();
  }


  GetMemberImage(event){
    this.MemberImageData = event.target.files[0]
  }

  CreateTeamForm() {
    this.TeamFormData = this._FormBuilder.group({
      Name: ['', [Validators.required, Validators.minLength(2)]],
      Role: ['',[Validators.required,Validators.minLength(2)]],
      MemberCategory: ['', Validators.required]
    });
  }

  AddTeamData(){
    let _FormData = new FormData();
    _FormData.append('Name',this.TeamFormData.get('Name').value);
    _FormData.append('Role',this.TeamFormData.get('Role').value);
    _FormData.append('MemberCategory',this.TeamFormData.get('MemberCategory').value);
    _FormData.append('TeamImage',this.MemberImageData);
    this._AboutUsSettingsService.PostAboutUsComponentTeam(_FormData).subscribe((docs:any)=>{
      let Message = docs.Message;
      this._FlashMessages.show(Message, { cssClass: 'alert-success', timeout: 10000 });
    })
    this.MemberImageField.nativeElement.value= null;
    this.TeamFormData.reset();
  }
}
