import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import {AdminRegisterUrl} from '../../../configuration/GlobalConstants';
import {AdminLoginUrl} from '../../../configuration/GlobalConstants';
import {GetAdminDataUrl} from '../../../configuration/GlobalConstants';
import {GetAdminDataByIdUrl} from '../../../configuration/GlobalConstants';
import {GetAdminLastLoginUrl} from '../../../configuration/GlobalConstants';
import {GetWebsiteStatsUrl} from '../../../configuration/GlobalConstants';
import {GetAllUsersUrl,PostWebSiteStatsUrl} from '../../../configuration/GlobalConstants';

@Injectable({
  providedIn: 'root'
})
export class AdminManagementService {

  constructor(private _HttpClient:HttpClient,private _Router:Router) { }

  PostWebSiteAnalytics(){
    const _PayLoad="DummyPayLoad";
    return this._HttpClient.post(PostWebSiteStatsUrl,_PayLoad);
  }

  GetAdminDataById(AdminId){
    return this._HttpClient.get(GetAdminDataByIdUrl + `${AdminId}`);
  }

  GetAdminData(){
  return this._HttpClient.get(GetAdminDataUrl);
  }

  AdminRegister(AdminData){
    return this._HttpClient.post(AdminRegisterUrl,AdminData);
  }

  AdminLogin(AdminData){
    return this._HttpClient.post(AdminLoginUrl,AdminData);
  }

  CheckIfUserisLoggedIn(){
    return localStorage.getItem('access_admin_token') != null;
  }
  
  LogIn(DataComingFromBackEnd){
    localStorage.setItem('admin_Id',DataComingFromBackEnd.UserData._id);
    localStorage.setItem('access_admin_token',DataComingFromBackEnd.Token);
        this._Router.navigate(['admin']);
  }

  LogOut(){
    localStorage.removeItem('admin_Id');
    localStorage.removeItem('access_admin_token');
    this._Router.navigate(['AdminLogin']);
  }
  
  GetAdminLastLogin(AdminId){
    var NewDate="Date";
    return this._HttpClient.put(GetAdminLastLoginUrl + `${AdminId}`,NewDate);
  }

  GetWebsiteStats(){
    var StatsDbId="5fef8d9ceaee8010b00fbcf0";
    return this._HttpClient.get(GetWebsiteStatsUrl);
  }

  GetAllUsers(){
    return this._HttpClient.get(GetAllUsersUrl);
  }
}
