import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FlashMessagesService } from 'angular2-flash-messages';
import { ECommerceDataServiceService } from 'src/app/Shared/Services/e-commerce-data-service.service';

@Component({
  selector: 'app-create-feature-card',
  templateUrl: './create-feature-card.component.html',
  styleUrls: ['./create-feature-card.component.css']
})
export class CreateFeatureCardComponent implements OnInit {
  //Class Properties
  SelectedImagesLength:any;
  DissabledButton:boolean;
  UpdateMessage:any;
  getData: FormGroup;
  ImageDataArray: any=[];
  message: string;
  _Sizes:any=[];
  FeatureCardDataLength: any;
//Class Propoerties

  constructor(private _Router:Router,private _FormBuilder:FormBuilder,private _FlashMessages:FlashMessagesService,private _ECommerceService:ECommerceDataServiceService) { this.createForm();}

  @ViewChild('FileSelect') FileSelect: ElementRef;
  
  ngOnInit(): void {
    this._ECommerceService.GetFeatureCards().subscribe((data:any)=>{
      this.FeatureCardDataLength=data.length;
      console.log(this.FeatureCardDataLength);
    })
  }

  createForm() {
    this.getData = this._FormBuilder.group({
      productName: ['', [Validators.required, Validators.minLength(2), Validators.maxLength(100)]],
      quantity: ['', [Validators.required, Validators.pattern(/^[0-9]*$/)]],
      price: ['', [Validators.required, Validators.pattern(/^[0-9]*$/)]],
      description: ['', Validators.required],
      altImage: ['',Validators.required],
      size: this._FormBuilder.array([]),
      fabrics:['',Validators.required],
      logo:['',Validators.required],
      color:['',Validators.required]
    });
  }

     //Getting The Sizes From CheckBox
     GetSizes(event){
      if (event.target.checked) {
        let _value = event.target.value;
        this._Sizes.push(_value);
      } else {
        this._Sizes = this._Sizes.filter(value=>value != event.target.value);
      }
    }
   //Getting The Sizes From CheckBox
   
   //Get Image with its Url
   GetFiles(event) {
    this.SelectedImagesLength=event.target.files.length;
    if (event.target.files.length>5) {
      this.DissabledButton=true;
      this.FileSelect.nativeElement.value= null;
      return null;
    } else {
      for(let i=0;i<event.target.files.length;i++){
        this.ImageDataArray.push(event.target.files[i]);
      }
      this.DissabledButton=false;
    }
  }
 //Get Image with its Url

  onRegisterSubmit() {
    if(this.FeatureCardDataLength >=3){
      this.message="New Release Posting Failure: Reason: The limit For Your New Release is 3";
      this._FlashMessages.show(this.message, { cssClass: 'alert-danger', timeout: 22000 });
    }
    else{
      const cardData = new FormData(); // Creating FormData object to send mulitpart data
      cardData.append('productName', this.getData.get('productName').value);//Appending values to the getData varibale from FormGroup
      cardData.append('quantity', this.getData.get('quantity').value);//Appending values to the getData varibale from FormGroup
      cardData.append('price', this.getData.get('price').value);//Appending values to the getData varibale from FormGroup
      cardData.append('description', this.getData.get('description').value);//Appending values to the getData varibale from FormGroup
      cardData.append('altImage', this.getData.get('altImage').value);//Appending values to the getData varibale from FormGroup
      cardData.append('fabrics', this.getData.get('fabrics').value)//Appending values to the getData varibale from FormGroup
      cardData.append('logo', this.getData.get('logo').value)//Appending values to the getData varibale from FormGroup
      cardData.append('color', this.getData.get('color').value)//Appending values to the getData varibale from FormGroup
      this._Sizes.map(SizeData=>{
        cardData.append('size', SizeData)//Appending values to the getData varibale from FormGroup
      })
      this.ImageDataArray.map(ImagesData=>{
        cardData.append('FeatureProductImage',ImagesData);//Appending values to the getData varibale from FormGroup
      })
      this._ECommerceService.CreateFeatureCard(cardData).subscribe((response: any) => {
        this.message = response.Message;
        this._FlashMessages.show(this.message, { cssClass: 'alert-success', timeout: 2000 });
        this.ngOnInit();
        });
        this.FileSelect.nativeElement.value= null;
        this.getData.reset();
        this.ImageDataArray=[];
  }
}

}
