import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {
  PostAboutUsComponentImagesDynamically,
  GetAboutUsComponentImagesDynamically,
  PostAboutUsComponentHeadingDynamically,
  GetAboutUsComponentHeadingDynamically,
  PostAboutUsComponentTeamDynamically,
  GetAboutUsComponentTeamDynamically
} from '../../../configuration/GlobalConstants';
@Injectable({
  providedIn: 'root'
})
export class AboutUsSettingsService {

  constructor(private _HttpClient: HttpClient) { }

  SendAboutUsComponentImages(data){
    return this._HttpClient.post(PostAboutUsComponentImagesDynamically,data);
  }

  GetAboutUsComponentImages(){
    return this._HttpClient.get(GetAboutUsComponentImagesDynamically);
  }

  PostAboutUsComponentHeadings(data){
    return this._HttpClient.post(PostAboutUsComponentHeadingDynamically,data);
  }

  GetAboutUsComponentHeadings(){
    return this._HttpClient.get(GetAboutUsComponentHeadingDynamically );
  }

  PostAboutUsComponentTeam(data){
    return this._HttpClient.post(PostAboutUsComponentTeamDynamically,data);
  }

  GetAboutUsComponentTeam(){
    return this._HttpClient.get(GetAboutUsComponentTeamDynamically);
  }
}
