import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AdminManagementService } from 'src/app/Shared/Services/admin-management.service';
import { MessengerService } from 'src/app/Shared/Services/messenger.service';
import {GetAdminImagesUrl} from 'src/configuration/GlobalConstants';

@Component({
  selector: 'app-admin-portal',
  templateUrl: './admin-portal.component.html',
  styleUrls: ['./admin-portal.component.css']
})
export class AdminPortalComponent implements OnInit {
  showSettingsList = false;
  user:any;
  URL=GetAdminImagesUrl;
  FilePath:any;
  AdminName:any;
  constructor(private _AdminManagementService:AdminManagementService, private _Router:Router,private _MessengerService:MessengerService) { }

  ngOnInit(): void {
    var AdminIdFromLocalStorage = localStorage.getItem('admin_Id');
    this._AdminManagementService.GetAdminDataById(AdminIdFromLocalStorage).subscribe((DataComingFromBackend:any) => {
      this.user=DataComingFromBackend.AdminData.Status;
      this.FilePath=DataComingFromBackend.AdminData.ImageUrl;
      this.AdminName=DataComingFromBackend.AdminData.Name;
    })
    // setTimeout(() => {
    //   this.LogOut();
    // }, 600000);
  }

  showSettings(){
    this.showSettingsList = !this.showSettingsList;
  }

  LogOut(){
    var AdminIdFromLocalStorage = localStorage.getItem('admin_Id');
    this._AdminManagementService.GetAdminLastLogin(AdminIdFromLocalStorage).subscribe(DataComingFromBackEnd=>{
    })
    this._AdminManagementService.LogOut();
  }
}
