import { Component, Input, OnInit } from '@angular/core';
import { AdminManagementService } from 'src/app/Shared/Services/admin-management.service';

@Component({
  selector: 'app-minimalist-chart',
  templateUrl: './minimalist-chart.component.html',
  styleUrls: ['./minimalist-chart.component.css']
})
export class MinimalistChartComponent implements OnInit {

 
  NewVisitors:any;
  Purchases:any;
  ActiveUsers:any;
  Returned:any;
  Clicks:any;
  constructor(private _AdminManagementService:AdminManagementService) { }

  ngOnInit(): void {
    this._AdminManagementService.GetWebsiteStats().subscribe((DataComingFromBackEnd:any)=>{
      this.NewVisitors=DataComingFromBackEnd[0].NewVisitors.VisitorCounter;
      this.Purchases=DataComingFromBackEnd[0].Purchases.PurchasesCounter;
      this.ActiveUsers=DataComingFromBackEnd[0].ActiveUsers.UserCounter;
      this.Returned=DataComingFromBackEnd[0].TotalReturns.RerutnsCounter;
      this.Clicks=DataComingFromBackEnd[0].ProductClicks.ClicksCounter;
    })
  }

}
